<template>
    <div id="app">
        <div class="window_con">
            <div v-show="!isHistory" style="box-sizing: border-box">
                <div class="title">
                    {{ isInner? currDay:'历史' }}
                </div>
                <div class="btn_close" @click="closeWindow">
                    <img src="@/assets/icon_close@2x.png" />
                </div>
                <div class="calendar_con">
                    <v-calendar is-expanded v-show="!isInner" :attributes='attrs' @dayclick='dayClicked'
                        @update:frompage="newMonth" @update:topage="newMonth">
                    </v-calendar>
                    <div class="currDayInfo" v-show="isInner">
                        <div class="btn_back" @click="backCalendar"><img src="@/assets/icon_back@2x.png" />返回</div>
                        <div class="header">
                            <div class="bread"><span class="back" @click="backCalendar">日期</span><span
                                    style="color: #2E4A66">></span><span>课程</span> </div>
                        </div>
                        <div class="list" v-loading="classLoading">
                            <vue-scroll>
                                <el-collapse v-for="cls in currDayList" :key="cls.classId">
                                    <el-collapse-item :title="cls.className" :name="cls.classId">
                                        <div class="item" :class="{active: activeId==item.id,disabled: item.interactRecordCount==0}" v-for="item in cls.classRecords" :key="item.id"
                                            @click="checkClass(item.id, item.interactRecordCount==0)">
                                            <div class="subject">{{ item.subjectName }}</div>
                                            
                                            <div class="time" v-if="item.endTime">
                                                <div class="count">，互动:&nbsp;<i>{{item.interactRecordCount}}&nbsp;</i>次</div>
                                                {{ item.startTime | formatDate}}—{{ item.endTime | formatDate}}
                                                
                                            </div>
                                            <div class="time" v-else>{{ item.startTime | formatDate}}进行中...</div>
                                        </div>
                                    </el-collapse-item>
                                </el-collapse>

                            </vue-scroll>
                        </div>
                    </div>
                </div>
            </div>
            <div v-show="isHistory" style="box-sizing: border-box">
                    <div class="title">随堂记录</div>
                    <div v-if="isHistory" class="btn_back" @click="backClass"><img src="@/assets/icon_back@2x.png" />返回</div>
                    <div class="records">
                        <vue-scroll>
                            <div class="item" v-for="item in records" :key="item.id">
                                <div class="info">
                                    <div class="info_left">
                                        <div class="type" v-if="item.type === 0">纸笔互动</div>
                                        <div class="type" v-else-if="item.type === 1||item.type === 10">【随堂测】<span
                                                v-if="item.ext">{{ item.ext | decodePaperName }}</span></div>
                                        <div class="type" v-else-if="item.type === 2">判断题</div>
                                        <div class="type" v-else-if="item.type === 3">单选</div>
                                        <div class="type" v-else-if="item.type === 4">多选</div>
                                        <div class="type" v-else-if="item.type === 5">抢答</div>
                                        <div class="user">{{ item.countStu }}人参与，
                                            <template
                                                v-if="item.status === 'uploading' && item.rate !== 1 && item.countStu">正在上传中...</template>
                                            <template
                                                v-if="item.type == 1 && item.rate !== 1 && item.countStu || item.type == 10 && item.rate !== 1 && item.countStu">正在批改中...</template>
                                            <template
                                                v-if="item.type == 0 && item.rate !== 1 && item.countStu">正在统计中...</template>
                                            <template
                                                v-if="item.endTime">结束于{{ item.endTime | formatDate}}，用时{{ item.withTime }}</template>
                                        </div>
                                    </div>
                                    <div class="btn_check" @click="checkTask(item.id, item.taskId, item.type)" v-if="!((item.type == 1||item.type == 10)&&item.countStu==0)">查看</div>
                                </div>
                                <div class="progress" v-if="item.countStu"
                                    v-show="item.type === 0 || item.type === 1|| item.type === 10 ">
                                    <div class="percent" :style="{width:  item.rate*100 +'%'}"></div>
                                </div>
                            </div>
                        </vue-scroll>
                    </div>
                    <div class="footer">
                        <div class="btn_close" @click="closeWindow" :class="{isHistoryPadding:isHistory}">
                            <img src="@/assets/icon_close@2x.png" />
                        </div>
                    </div>
            </div>
        </div>
    </div>
</template>
<script>
    import { formatDate } from '@/assets/date.js';
    import utils from '@/assets/utils.js';
    import API from '@/assets/api.js'
    import axios from 'axios'
    var Base64 = require('js-base64').Base64;
    export default {
        name: 'App',
        data() {
            return {
                classId: 0,
                userId: 0,
                activeId: 0,
                isInner: false,
                isHistory: false,
                classLoading: false,
                currDay: "",
                currDayList: [],
                records: "",
                attrs: [
                    {
                        highlight: {
                            backgroundColor: '#FE9400',
                            // Other properties are available too, like `height` & `borderRadius`
                        },
                        dates: []
                    },
                    {
                        key: 'today',
                        // dot: {
                        //     backgroundColor: '#9DBEDA', // Green dot
                        // },
                        dates: [
                            new Date()
                        ],
                    },
                ],
            }
        },
        filters: {
            formatDate(time) {
                let date = new Date(time);
                return formatDate(date, 'hh:mm');
            },
            decodePaperName(str) {
                return Base64.decode(str.split(",")[0].split(":")[1]);
            }
        },
        beforeDestroy () {
            localStorage.removeItem("currDay")
        },
        mounted() {
            //注册需要外部调用的方法
            //获取url参数
            let classId = utils.getUrlPrams("classId")
            let userId = utils.getUrlPrams("userId")
            let currDay = localStorage.getItem("currDay")
            console.log(classId, userId)
            this.classId = classId ? classId : ""
            this.userId = userId
            if (currDay) {
                this.dayClicked(JSON.parse(currDay))
                this.isInner = true
            }

            let today = new Date()
            let date = this.formatDate(today)
            this.loadHeightLight(date)
            // this.loadHeightLight(date)

        },
        methods: {
            closeWindow() {
                localStorage.removeItem("currDay")
                window.cefQuery({
                    request: "closeWindowCalendar",   // 代表请求方法 example事件名称 data 为传入参数
                    onSuccess: function (response) {
                        console.log("invoke success response = " + response);
                    },
                    onFailure: function (ecode, emsg) {
                        console.log("invoke fail code = " + ecode + " msg:" + emsg);
                    }
                });
            },
            checkTask(id, taskId, type) {
                let value = "";
                if (type === 0 || type === 1 || type === 10) {
                    value = taskId
                } else {
                    value = id
                }
                console.log(value)
                window.cefQuery({
                    request: "checkTask:" + value + "&" + type,   // 代表请求方法 example事件名称 data 为传入参数
                    onsuccess: function (response) {
                        console.log("invoke success response = " + response);
                    },
                    onFailure: function (ecode, emsg) {
                        console.log("invoke fail code = " + ecode + " msg:" + emsg);
                    }
                });
            },
            getRecords(id) {
                var that = this
                axios.get(API.api_tl + 'api/selClassRecord', {
                    params: {
                        classingId: id
                    }
                }).then(function (res) {
                    console.log(res.data.data)
                    let records = res.data.data
                    if (records) {
                        that.records = records
                    }
                  
                   
                }).catch(function (error) {
                    console.log(error);
                });
            },
            checkClass(id,isEmpty) {
                if(!isEmpty){
                    this.isHistory = true
                    this.activeId = id
                    this.getRecords(id)
                }
                
            },
            newMonth(page) {
                console.log(page)
                let year = page.year
                let month = page.month < 10 ? "0" + page.month : "" + page.month
                let date = year + month
                this.loadHeightLight(date)

            },
            backCalendar() {
                this.isInner = false
                this.activeId = 0 
                localStorage.removeItem("currDay")
            },
            backClass() {
                this.isHistory = false
                this.records = []
            },
            formatDate(time) {
                let date = new Date(time);
                return formatDate(date, 'yyyyMM');
            },
            loadHeightLight(date) {
                var that = this
                console.log(date)
                axios.get(API.api_tl + 'api/selCal', {
                    params: {
                        // taskIds: that.taskIds.join(",")
                        classId: this.classId,
                        userId: this.userId,
                        date: date
                    }
                }).then(function (res) {
                    console.log(res.data.data)
                    let dates = res.data.data
                    let selected = []
                    for (let date of dates) {
                        let year = date.substring(0, 4)
                        let month = date.substring(4, 6).indexOf(0) === 0 ? parseInt(date.substring(5, 6)) - 1 : parseInt(date.substring(4, 6)) - 1
                        let day = date.substring(6, 8)
                        console.log(year, month, day)
                        selected.push(new Date(year, month, day))
                    }
                    that.attrs[0].dates = selected

                }).catch(function (error) {
                    console.log(error);
                });
            },
            dayClicked(currDay) {
                this.classLoading = true
                console.log(currDay)
                this.isInner = true
                localStorage.setItem("currDay", JSON.stringify(currDay))
                let year = currDay.year
                let month = currDay.month > 9 ? "" + currDay.month : "0" + currDay.month
                console.log(currDay.day)
                let day = currDay.day > 9 ? "" + currDay.day : "0" + currDay.day
                let date = year + month + day
                this.currDay = year + "-" + month + "-" + day

                var that = this
                console.log(date)
                axios.get(API.api_tl + 'api/selDayRecord', {
                    params: {
                        // taskIds: that.taskIds.join(",")
                        classId: that.classId,
                        userId: that.userId,
                        date: date
                    }
                }).then(function (res) {
                    that.classLoading = false
                    that.currDayList = res.data.data
                    console.log(that.currDayList)
                }).catch(function (error) {
                    console.log(error);
                });
            },
        }
    }
</script>
<style>
    * {
        margin: 0;
        padding: 0;
    }

    body {
        background: transparent;
        display: flex;
        justify-content: space-around;
        align-items: center;
    }

    #app {
        font-family: "Helvetica Neue", Helvetica, "PingFang SC", "Hiragino Sans GB", "Microsoft YaHei", "微软雅黑", Arial, sans-serif;
        -webkit-font-smoothing: antialiased;
        -moz-osx-font-smoothing: grayscale;
        text-align: center;
        color: #2D4A66;
        width: 100%;
        height: 100%;
    }

    .c-pane-container {
        color: #2D4A66 !important;
    }

    .c-weekday {
        color: #2D4A66 !important;
    }

    .el-collapse-item__header {
        height: 0.6rem !important;
        padding: 0 0.2rem;
        text-align: left;
        font-size: 0.16rem !important;
        line-height: 0.6rem !important;
        color: #2D4A66 !important;
        border-bottom: 1px solid #CCCCCC
    }

    .c-day-content-wrapper {
        border-left: none !important;
    }

    .el-collapse-item__content {
        padding-bottom: 0 !important
    }
</style>
<style scoped>
    .window_con {
        box-sizing: border-box;
        width: 100%;
        height: 100vh;
        border-radius: 0.2rem;
        background: #fff;
        display: flex;
        flex-direction: column;
    }

    .title {
        padding-top: 0.3rem;
    }

    .btn_close {
        padding: 0.2rem;
    }

    .calendar_con {
        height: calc(100vh - 1rem);
        /* padding: 0 0.3rem 0.3rem 0.3rem; */
    }

    .currDayInfo {
        box-sizing: border-box;
        padding: 0 0.4rem;
    }

    .currDayInfo .header {
        height: 0.7rem;
        position: relative;
        border-bottom: 0.01rem solid #ccc;
    }

    .currDayInfo .header .bread {
        position: absolute;
        left: 0.2rem;
        top: 0;
    }

    .currDayInfo .currDate {
        text-align: center;
        font-size: 0.22rem;
        color: #2D4A66;
        line-height: 70px;
    }

    .currDayInfo .header .bread span {
        font-size: 0.16rem;
        display: inline-block;
        color: #9DBEDA;
        line-height: 0.7rem;
        padding: 0 0.1rem;
    }

    .currDayInfo .header .bread .back {
        padding-left: 0;
        cursor: pointer;
    }

    .currDayInfo .list {
        box-sizing: border-box;
        height: calc(100vh - 2.1rem);
    }

    .currDayInfo .list .item {
        height: 0.6rem;
        line-height: 0.6rem;
        padding: 0 0.44rem;
        font-size: 0.16rem;
        color: #2D4A66;
        cursor: pointer;
        background: rgba(245, 248, 251, 1);
        border-bottom: 1px dashed #C0C8D1;
    }

    .currDayInfo .list .item.active {
        background: #E1EBF3;
    }
    .currDayInfo .list .item.disabled {
        opacity: 0.5;
        pointer-events: none;
    }

    .currDayInfo .list .item:hover {
        background: #EBEEF5;
        transition: 0.2s all linear;
    }

    .currDayInfo .list .item .time {
        float: right;
    }

    .currDayInfo .list .item .subject {
        float: left;
    }
    .currDayInfo .list .item .count {
        float: right;
    }
    .currDayInfo .list .item .count i{
        font-style: normal;
        color: #00e4ff;
    }

    .btn_back {
        font-size: 0.16rem;
        width: 1rem;
        padding: 0.2rem;
        text-align: left;
        color: #2E4A66;
        vertical-align: middle;
        position: absolute;
        left: 0;
        top: 0;
    }

    .btn_back img {
        display: inline-block;
        width: 0.18rem;
        height: 0.18rem;
        vertical-align: middle;
        margin-right: 0.1rem;
    }
    .isHistoryPadding{
        padding: 0.2rem
    }

    .records {
        width: 100%;
        height: 80vh;
        margin: 0 auto;
        box-sizing: border-box;
        padding: 0.2rem
    }

    .records .item {
        height: 1rem;
        border-bottom: 1px solid rgba(51, 81, 102, .15);
        margin: 0 0.3rem;
    }

    .info {
        display: flex;
        flex-direction: row;
        align-items: center;
        height: 1rem;
    }

    .info_left {
        flex: 1;
        text-align: left;
    }

    .info_left .type {
        font-size: 0.16rem;
        font-weight: 400;
        color: #2D4A66;
        line-height: 0.33rem;
        margin-bottom: 0.1rem;
    }

    .info_left .user {
        font-size: 0.14rem;
        font-weight: 400;
        color: #AEBDD6;
        line-height: 1.5;
    }

    .btn_check {
        font-size: 0.16rem;
        font-weight: 400;
        color: #2D4A66;
        line-height: 0.35rem;
        width: 0.65rem;
        height: 0.35rem;
        border-radius: 0.03rem;
        cursor: pointer;
        border: 1px solid #8BADCE;
    }

    .progress {
        height: 0.04rem;
        background: #9DBEDA;
        border-radius: 0.02rem;
    }

    .progress .percent {
        height: 0.04rem;
        background: #00D97F;
        border-radius: 0.02rem;
        opacity: 0.8025;
        transition: 0.3s all linear;
    }

    .footer {
        height: 10vh;
        display: flex;
        justify-content: space-around;
        align-items: center;
    }
</style>